@media (max-width:992px) {
    .title {
        font-size: 45px;
    }
}

@media (max-width:768px) {
    .title {
        font-size: 40px;
        line-height: 40px;
    }
}

@media (max-width:576px) {
    .title {
        font-size: 30px;
        line-height: 30px;
        width: unset;
    }
}